import React, { useState } from 'react';

function Output(props) {
  const [isOpen, setIsOpen] = useState(true);

  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };

  const modifiedLines = props.output.split('\n').map((line) => {
    return `$ ${line}`;
  });
  
  const result = modifiedLines.join('<br />');

  return (
    <div>
      <button className='btn btn-dark' onClick={toggleDropdown}>
        {isOpen ? 'Close Output' : 'View output'}
      </button>
      {isOpen && (
        <div className='output'>
          {
            <span dangerouslySetInnerHTML={{__html: result}}></span>
          }
        </div>
      )}
    </div>
  );
}

export default Output;
