import React, { useEffect, useState } from "react";
import CodeMirror from "@uiw/react-codemirror";
import { createTheme } from "@uiw/codemirror-themes";
import { tags as t } from "@lezer/highlight";
import { StreamLanguage } from "@codemirror/language";
import { go } from "@codemirror/legacy-modes/mode/go";
import axios from "axios";

const myTheme = createTheme({
  theme: "light",
  settings: {
    background: "#ffffff",
    foreground: "#75baff",
    caret: "#5d00ff",
    selection: "#036dd626",
    selectionMatch: "#036dd626",
    lineHighlight: "#8a91991a",
    gutterBackground: "#fff",
    gutterForeground: "#8a919966",
    selectionMatch: "#e8e8e8",
  },
  styles: [
    { tag: t.comment, color: "#787b8099" },
    { tag: t.variableName, color: "#0080ff" },
    { tag: [t.string, t.special(t.brace)], color: "#5c6166" },
    { tag: t.number, color: "#5c6166" },
    { tag: t.bool, color: "#5c6166" },
    { tag: t.null, color: "#5c6166" },
    { tag: t.keyword, color: "#5c6166" },
    { tag: t.operator, color: "#5c6166" },
    { tag: t.className, color: "#5c6166" },
    { tag: t.definition(t.typeName), color: "#5c6166" },
    { tag: t.typeName, color: "#5c6166" },
    { tag: t.angleBracket, color: "#5c6166" },
    { tag: t.tagName, color: "#5c6166" },
    { tag: t.attributeName, color: "#5c6166" },
  ],
});

const defaultValue = `package main

import "fmt"

func main() {
  fmt.Println("Welcome to the go playground by Prakhar Rai.")
}
`;

export default function Editor({set}) {
  const [code, setCode] = useState();
  useEffect(() => {
    setCode(defaultValue);
  }, []);
  
  return (
    <>
      <div style={{marginBottom: "1rem"}}>
        <CodeMirror
          value={defaultValue}
          theme={myTheme}
          extensions={[StreamLanguage.define(go)]}
          onChange={(value, valueUpdate) => setCode(value)}
        />
      </div>
      <button
        className="btn btn-primary"
        onClick={() => {
          const formData = new FormData();
          formData.append('body', code);
          formData.append('version', 2);

          // const url = "https://go.dev/_/compile?backend=";
          axios.post("_/compile?backend=", formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          })
          .then(response => {
            // console.log('POST request successful:', response.data);
            set(response.data.Events[0].Message)
          })
          .catch(error => {
            console.error('Error making POST request:', error);
          });
        }}
      >
        Run &nbsp; 
      </button>
    </>
  );
}
